<script>
import { ref, reactive, onMounted } from 'vue'
import { message, Modal } from 'ant-design-vue'
import { 
  PlusOutlined, 
  SearchOutlined,
  AppleOutlined,
  AndroidOutlined,
  GlobalOutlined,
  TeamOutlined,
  CustomerServiceOutlined,
  SettingOutlined,
  UserOutlined,
  UpOutlined,
  DownOutlined
} from '@ant-design/icons-vue'
import { feedbackAPI } from '@/utils/api'

export default {
  name: 'FeedbackManagement',
  components: {
    PlusOutlined,
    SearchOutlined,
    AppleOutlined,
    AndroidOutlined,
    GlobalOutlined,
    TeamOutlined,
    CustomerServiceOutlined,
    SettingOutlined,
    UserOutlined,
    UpOutlined,
    DownOutlined,
  },
  setup() {
    const loading = ref(false)
    const analyzing = ref(false)
    const reanalyzing = ref(false)
    const feedbackList = ref([])
    const selectedFeedback = ref(null)
    const showCreateDialog = ref(false)
    const showViewDialog = ref(false)
    const editingFeedback = ref(null)
    const feedbackFormRef = ref(null)
    
    const searchQuery = ref('')
    const statusFilter = ref(undefined)
    const categoryFilter = ref(undefined)
    
    const pagination = ref({
      current: 1,
      pageSize: 50,
      total: 0,
      showSizeChanger: true,
      showQuickJumper: true,
      showTotal: (total, range) => `Á¨¨ ${range[0]}-${range[1]} Êù°ÔºåÂÖ± ${total} Êù°`,
      pageSizeOptions: ['20', '50', '100', '200'],
    })

    const feedbackForm = reactive({
      title: '',
      content: '',
      category: undefined,
      priority: 'normal',
      email: ''
    })

    const feedbackColumns = [
      { 
        title: 'ÂèçÈ¶àÂÜÖÂÆπ', 
        dataIndex: 'feedback_content', 
        key: 'feedback_content', 
        ellipsis: true,
        width: '55%'
      },
      { 
        title: 'Êù•Ê∫êÊ∏†ÈÅì', 
        dataIndex: 'source', 
        key: 'source', 
        width: '12%',
        align: 'center'
      },
      { 
        title: 'ÈóÆÈ¢òÂàÜÁ±ª', 
        dataIndex: 'category', 
        key: 'category', 
        width: '10%',
        align: 'center'
      },
      { 
        title: 'Â§ÑÁêÜÁä∂ÊÄÅ', 
        dataIndex: 'status', 
        key: 'status', 
        width: '10%',
        align: 'center'
      },
      { 
        title: '‰ºòÂÖàÁ∫ß', 
        dataIndex: 'priority', 
        key: 'priority', 
        width: '8%',
        align: 'center'
      },
      { 
        title: 'Êèê‰∫§Êó∂Èó¥', 
        dataIndex: 'created_at', 
        key: 'created_at', 
        width: '10%',
        align: 'center'
      },
    ]

    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log('ÈÄâ‰∏≠ÁöÑÂèçÈ¶à:', selectedRows)
      },
    }

    const getStatusColor = (status) => {
      const colors = {
        pending: 'orange',
        processing: 'blue',
        completed: 'green',
        rejected: 'red'
      }
      return colors[status] || 'default'
    }

    const getStatusLabel = (status) => {
      const labels = {
        pending: 'ÂæÖÂ§ÑÁêÜ',
        processing: 'Â§ÑÁêÜ‰∏≠',
        completed: 'Â∑≤ÂÆåÊàê',
        rejected: 'Â∑≤ÊãíÁªù'
      }
      return labels[status] || status
    }

    const getCategoryColor = (category) => {
      const colors = {
        feature: 'blue',
        feature_request: 'blue',
        bug: 'red',
        bug_report: 'red',
        ux: 'green',
        ux_complaint: 'green',
        performance: 'orange',
        question: 'cyan',
        praise: 'green',
        general: 'default',
        other: 'default'
      }
      return colors[category] || 'default'
    }

    const getCategoryLabel = (category) => {
      const labels = {
        feature: 'ÂäüËÉΩÂª∫ËÆÆ',
        feature_request: 'ÂäüËÉΩÈúÄÊ±Ç',
        bug: 'ÈóÆÈ¢òÊä•Âëä',
        bug_report: 'BugÊä•Âëä',
        ux: 'Áî®Êà∑‰ΩìÈ™å',
        ux_complaint: 'UXÊäïËØâ',
        performance: 'ÊÄßËÉΩ‰ºòÂåñ',
        question: 'Âí®ËØ¢ÈóÆÈ¢ò',
        praise: 'Ë°®Êâ¨ÂèçÈ¶à',
        general: 'ÈÄöÁî®ÂèçÈ¶à',
        other: 'ÂÖ∂‰ªñ'
      }
      return labels[category] || category
    }

    const getPriorityColor = (priority) => {
      const colors = {
        low: 'green',      // ‰Ωé‰ºòÂÖàÁ∫ß - ÁªøËâ≤
        medium: 'orange',  // ‰∏≠Á≠â‰ºòÂÖàÁ∫ß - Ê©ôËâ≤
        normal: 'orange',  // ÊôÆÈÄö‰ºòÂÖàÁ∫ß - Ê©ôËâ≤
        high: 'red',       // È´ò‰ºòÂÖàÁ∫ß - Á∫¢Ëâ≤
        urgent: 'volcano'  // Á¥ßÊÄ•‰ºòÂÖàÁ∫ß - Ê∑±Á∫¢Ëâ≤
      }
      return colors[priority] || 'orange'
    }

    const getPriorityText = (priority) => {
      const texts = {
        low: '‰Ωé',
        medium: '‰∏≠Á≠â',
        normal: 'ÊôÆÈÄö',
        high: 'È´ò',
        urgent: 'Á¥ßÊÄ•'
      }
      return texts[priority] || priority
    }

    // Ëé∑ÂèñÂèçÈ¶àÊ†áÈ¢ò - ‰ºòÂÖàËé∑ÂèñÁã¨Á´ãÊ†áÈ¢òÂ≠óÊÆµ
    const getFeedbackTitle = (record) => {
      // ‰ºòÂÖà‰ΩøÁî®titleÂ≠óÊÆµÔºåÂ¶ÇÊûúÊ≤°ÊúâÂàô‰ªéÂÜÖÂÆπÊèêÂèñÂâçÂá†‰∏™Â≠ó‰Ωú‰∏∫Ê†áÈ¢ò
      if (record.title && record.title.trim()) {
        const cleanTitle = record.title.replace(/\s+/g, ' ').trim()
        return cleanTitle.length > 30 ? cleanTitle.substring(0, 30) + '...' : cleanTitle
      }
      
      // Â¶ÇÊûúÊ≤°ÊúâÁã¨Á´ãÊ†áÈ¢òÔºå‰ªéÂÜÖÂÆπ‰∏≠ÊèêÂèñÂâç20‰∏™Â≠óÁ¨¶‰Ωú‰∏∫Ê†áÈ¢ò
      const content = record.original_text || record.content || record.original_content || record.processed_text || ''
      if (content) {
        const cleanContent = content.replace(/\s+/g, ' ').trim()
        return cleanContent.length > 20 ? cleanContent.substring(0, 20) + '...' : cleanContent
      }
      
      return 'Êó†Ê†áÈ¢ò'
    }

    // Ëé∑ÂèñÂèçÈ¶àÂÜÖÂÆπ - ÊòæÁ§∫ÂÆåÊï¥ÂèçÈ¶àÂÜÖÂÆπ
    const getFeedbackContent = (record) => {
      const content = record.original_text || record.content || record.original_content || record.processed_text || ''
      if (!content) return 'ÊöÇÊó†ÂÜÖÂÆπ'
      
      // ‰øùÁïôÊç¢Ë°åÁ¨¶ÔºåÂè™ÊõøÊç¢Â§ö‰ΩôÁöÑÁ©∫ÁôΩ
      const cleanContent = content.replace(/[ \t]+/g, ' ').trim()
      
      if (record.expanded) {
        return cleanContent
      }
      
      return cleanContent.length > 120 ? cleanContent.substring(0, 120) + '...' : cleanContent
    }

    // Âà§Êñ≠ÊòØÂê¶ÈúÄË¶ÅÂ±ïÂºÄ
    const needsExpansion = (record) => {
      const content = record.original_text || record.content || record.original_content || record.processed_text || ''
      const cleanContent = content.replace(/[ \t]+/g, ' ').trim()
      return cleanContent.length > 120
    }

    // ÂàáÊç¢Â±ïÂºÄÁä∂ÊÄÅ
    const toggleExpansion = (record) => {
      // ‰ΩøÁî®VueÁöÑÂìçÂ∫îÂºèÊõ¥Êñ∞
      const index = feedbackList.value.findIndex(item => item._id === record._id)
      if (index !== -1) {
        feedbackList.value[index].expanded = !feedbackList.value[index].expanded
      }
    }

    // Êù•Ê∫êÈÖçÁΩÆ - Ë°å‰∏öÊ†áÂáÜ
    const getSourceIcon = (source) => {
      const icons = {
        app_store: 'AppleOutlined',
        google_play: 'AndroidOutlined', 
        social_media: 'GlobalOutlined',
        internal: 'TeamOutlined',
        zendesk: 'CustomerServiceOutlined',
        user_survey: 'SettingOutlined'
      }
      return icons[source] || 'SettingOutlined'
    }

    const getSourceLabel = (source) => {
      const labels = {
        app_store: 'App Store',
        ios_app_store: 'iOS App Store',
        android_app_store: 'AndroidÂ∫îÁî®Â∏ÇÂú∫',
        google_play: 'Google Play',
        huawei_app_store: 'Âçé‰∏∫Â∫îÁî®Â∏ÇÂú∫',
        xiaomi_app_store: 'Â∞èÁ±≥Â∫îÁî®Â∏ÇÂú∫',
        vivo_app_store: 'vivoÂ∫îÁî®Â∏ÇÂú∫',
        oppo_app_store: 'OPPOÂ∫îÁî®Â∏ÇÂú∫',
        meizu_app_store: 'È≠ÖÊóèÂ∫îÁî®Â∏ÇÂú∫',
        social_media: 'Á§æ‰∫§Âπ≥Âè∞',
        internal: 'ÂÜÖÈÉ®Á≥ªÁªü',
        zendesk: 'ÂÆ¢ÊúçÂ∑•Âçï',
        user_survey: 'Áî®Êà∑Ë∞ÉÁ†î'
      }
      return labels[source] || source || 'Unknown'
    }

    // ÊÉÖÊÑüÂàÜÊûêÊòæÁ§∫
    const getSentimentColor = (sentiment) => {
      const colors = {
        positive: 'green',
        negative: 'red',
        neutral: 'default'
      }
      return colors[sentiment] || 'default'
    }

    const getSentimentLabel = (sentiment) => {
      const labels = {
        positive: 'Ê≠£Èù¢',
        negative: 'Ë¥üÈù¢', 
        neutral: '‰∏≠ÊÄß'
      }
      return labels[sentiment] || sentiment
    }

    // ÂàÜÊûêÊñπÊ≥ïÊòæÁ§∫
    const getAnalysisMethodColor = (method) => {
      const colors = {
        'multi_model_ai': 'green',
        'intelligent_filter': 'orange',
        'content_empty': 'red',
        'manual': 'blue'
      }
      return colors[method] || 'default'
    }

    const getAnalysisMethodLabel = (method) => {
      const labels = {
        'multi_model_ai': 'AIÂàÜÊûê',
        'intelligent_filter': 'È¢ÑÁ≠õÈÄâ',
        'content_empty': 'ÂÜÖÂÆπÁ©∫',
        'manual': 'ÊâãÂä®'
      }
      return labels[method] || method
    }

    // Ê®°ÂûãÊòæÁ§∫
    const getModelLabel = (model) => {
      if (!model || model === 'none') return ''
      
      // ÊèêÂèñÊ®°ÂûãÁÆÄÁß∞
      if (model.includes('gemini')) {
        return 'Gemini'
      } else if (model.includes('gpt')) {
        return 'GPT'
      } else if (model.includes('claude')) {
        return 'Claude'
      } else if (model === 'default') {
        return 'ÈªòËÆ§'
      } else if (model === 'intelligent_filter') {
        return 'È¢ÑÁ≠õÈÄâ'
      }
      return model.substring(0, 10) // ÈôêÂà∂ÈïøÂ∫¶
    }

    const getSourceColor = (source) => {
      const colors = {
        app_store: 'blue',
        google_play: 'green',
        social_media: 'purple',
        internal: 'orange',
        zendesk: 'cyan',
        user_survey: 'geekblue'
      }
      return colors[source] || 'default'
    }

    // Áä∂ÊÄÅÂæΩÁ´† - Ë°å‰∏öÊ†áÂáÜ
    const getStatusBadge = (status) => {
      const badges = {
        pending: 'processing',
        in_progress: 'processing', 
        resolved: 'success',
        closed: 'default'
      }
      return badges[status] || 'default'
    }

    // Ëé∑ÂèñÁä∂ÊÄÅÊ†∑ÂºèÁ±ª
    const getStatusClass = (status) => {
      const classes = {
        pending: 'status-pending',
        in_progress: 'status-processing', 
        resolved: 'status-success',
        closed: 'status-default'
      }
      return classes[status] || 'status-default'
    }

    // Áõ∏ÂØπÊó∂Èó¥ÊòæÁ§∫
    const getRelativeTime = (timeStr) => {
      if (!timeStr) return '--'
      
      try {
        const date = new Date(timeStr)
        const now = new Date()
        const diff = now.getTime() - date.getTime()
        
        const minutes = Math.floor(diff / (1000 * 60))
        const hours = Math.floor(diff / (1000 * 60 * 60))
        const days = Math.floor(diff / (1000 * 60 * 60 * 24))
        
        if (minutes < 1) return 'ÂàöÂàö'
        if (minutes < 60) return `${minutes}ÂàÜÈíüÂâç`
        if (hours < 24) return `${hours}Â∞èÊó∂Ââç`
        if (days < 7) return `${days}Â§©Ââç`
        if (days < 30) return `${Math.floor(days / 7)}Âë®Ââç`
        return `${Math.floor(days / 30)}‰∏™ÊúàÂâç`
      } catch (error) {
        return '--'
      }
    }

    // ÁªùÂØπÊó∂Èó¥ÊòæÁ§∫
    const formatAbsoluteTime = (timeStr) => {
      if (!timeStr) return '--'
      
      try {
        const date = new Date(timeStr)
        const month = (date.getMonth() + 1).toString().padStart(2, '0')
        const day = date.getDate().toString().padStart(2, '0')
        const hour = date.getHours().toString().padStart(2, '0')
        const minute = date.getMinutes().toString().padStart(2, '0')
        
        return `${month}-${day} ${hour}:${minute}`
      } catch (error) {
        return '--'
      }
    }

    const loadFeedback = async () => {
      loading.value = true
      try {
        // ÊûÑÂª∫Êü•ËØ¢ÂèÇÊï∞
        const params = {
          skip: (pagination.value.current - 1) * pagination.value.pageSize,
          limit: pagination.value.pageSize
        }
        
        // Ê∑ªÂä†ËøáÊª§Êù°‰ª∂
        if (statusFilter.value) {
          params.status = statusFilter.value
        }
        if (categoryFilter.value) {
          params.source = categoryFilter.value
        }
        
        // Ë∞ÉÁî®ÁúüÂÆûAPI
        const response = await feedbackAPI.getFeedbacks(params)
        
        if (response.data) {
          feedbackList.value = (response.data.data || []).map(item => ({
            ...item,
            expanded: false // ÂàùÂßãÂåñÂ±ïÂºÄÁä∂ÊÄÅ
          }))
          pagination.value.total = response.data.total || 0
          
          console.log('‚úÖ ‰ªéÊï∞ÊçÆÂ∫ìÂä†ËΩΩÂèçÈ¶àÊï∞ÊçÆ:', {
            ÊÄªÊï∞: pagination.value.total,
            ÂΩìÂâçÈ°µ: feedbackList.value.length,
            È°µÁ†Å: pagination.value.current
          })
        }
      } catch (error) {
        console.error('‚ùå Âä†ËΩΩÂèçÈ¶àÊï∞ÊçÆÂ§±Ë¥•:', error)
        message.error('Âä†ËΩΩÂèçÈ¶àÊï∞ÊçÆÂ§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
        
        // Âá∫ÈîôÊó∂Ê∏ÖÁ©∫Êï∞ÊçÆ
        feedbackList.value = []
        pagination.value.total = 0
      } finally {
        loading.value = false
      }
    }

    const handleSearch = () => {
      pagination.value.current = 1
      loadFeedback()
    }

    const resetFilters = () => {
      searchQuery.value = ''
      statusFilter.value = undefined
      categoryFilter.value = undefined
      pagination.value.current = 1
      loadFeedback()
    }

    const handleTableChange = (pag, filters, sorter) => {
      pagination.value = pag
      loadFeedback()
    }

    const viewFeedback = (feedback) => {
      selectedFeedback.value = feedback
      showViewDialog.value = true
    }

    const editFeedback = (feedback) => {
      editingFeedback.value = feedback
      Object.assign(feedbackForm, {
        title: feedback.title,
        content: feedback.content,
        category: feedback.category,
        priority: feedback.priority,
        email: feedback.email
      })
      showCreateDialog.value = true
    }

    const analyzeFeedback = (feedback) => {
      Modal.confirm({
        title: 'Á°ÆËÆ§ÂàÜÊûê',
        content: 'Á°ÆÂÆöË¶ÅÂØπÊ≠§ÂèçÈ¶àËøõË°åÊô∫ËÉΩÂàÜÊûêÂêóÔºü',
        onOk() {
          message.success('ÂàÜÊûê‰ªªÂä°Â∑≤ÂêØÂä®')
          loadFeedback()
        },
      })
    }

    const deleteFeedback = async (feedback) => {
      Modal.confirm({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: `Á°ÆÂÆöË¶ÅÂà†Èô§ÂèçÈ¶à"${feedback.title}"ÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ`,
        okType: 'danger',
        async onOk() {
          try {
            await feedbackAPI.deleteFeedback(feedback.id)
            message.success('ÂèçÈ¶àÂà†Èô§ÊàêÂäü')
            loadFeedback()
          } catch (error) {
            message.error('Âà†Èô§Â§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
          }
        },
      })
    }

    const saveFeedback = () => {
      feedbackFormRef.value
        .validate()
        .then(() => {
          if (editingFeedback.value) {
            message.success('ÂèçÈ¶àÊõ¥Êñ∞ÊàêÂäü')
          } else {
            message.success('ÂèçÈ¶àÂàõÂª∫ÊàêÂäü')
          }
          showCreateDialog.value = false
          resetForm()
          loadFeedback()
        })
        .catch(() => {
          message.error('ËØ∑Ê£ÄÊü•Ë°®ÂçïÂÜÖÂÆπ')
        })
    }

    const cancelEdit = () => {
      showCreateDialog.value = false
      resetForm()
    }

    const resetForm = () => {
      Object.assign(feedbackForm, {
        title: '',
        content: '',
        category: undefined,
        priority: 'normal',
        email: ''
      })
      editingFeedback.value = null
      feedbackFormRef.value?.resetFields()
    }

    onMounted(() => {
      loadFeedback()
    })

    // ‰∏ÄÈîÆAIÂàÜÊûêÂäüËÉΩ
    const triggerAnalyzeAll = async () => {
      analyzing.value = true
      try {
        const response = await feedbackAPI.analyzeAllUnprocessed()
        const data = response.data
        
        if (data.unanalyzed_count === 0) {
          message.success('üéâ ÊâÄÊúâÂèçÈ¶àÊï∞ÊçÆÈÉΩÂ∑≤ÂÆåÊàêAIÂàÜÊûêÔºÅ')
        } else {
          message.success(`üöÄ ÊàêÂäüÂêØÂä®AIÂàÜÊûêÔºÅÊ≠£Âú®Â§ÑÁêÜ ${data.unanalyzed_count} Êù°ÂèçÈ¶àÊï∞ÊçÆÔºåÈ¢ÑËÆ°ÈúÄË¶Å ${data.analysis_details.estimated_time}`)
          
          // Âª∂ËøüÂà∑Êñ∞Êï∞ÊçÆÔºåÁªôAIÂàÜÊûê‰∏Ä‰∫õÊó∂Èó¥
          setTimeout(() => {
            loadFeedback()
          }, 3000)
        }
        
        console.log('‚úÖ AIÂàÜÊûê‰ªªÂä°ÂêØÂä®ÊàêÂäü:', data)
      } catch (error) {
        console.error('‚ùå AIÂàÜÊûêÂêØÂä®Â§±Ë¥•:', error)
        message.error('AIÂàÜÊûêÂêØÂä®Â§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
      } finally {
        analyzing.value = false
      }
    }

    // Âº∫Âà∂ÈáçÊñ∞ÂàÜÊûêÂäüËÉΩÔºà‰ΩøÁî®ÊîπËøõÁöÑAI promptÔºâ
    const triggerForceReanalyze = async () => {
      reanalyzing.value = true
      try {
        const response = await feedbackAPI.forceReanalyzeAll()
        const data = response.data
        
        message.success(`üîÑ ${data.message}`)
        
        // Âª∂ËøüÂà∑Êñ∞Êï∞ÊçÆÔºåÁªôAIÈáçÊñ∞ÂàÜÊûê‰∏Ä‰∫õÊó∂Èó¥
        setTimeout(() => {
          loadFeedback()
        }, 5000)
        
        console.log('‚úÖ Âº∫Âà∂ÈáçÊñ∞ÂàÜÊûêÂêØÂä®ÊàêÂäü:', data)
      } catch (error) {
        console.error('‚ùå Âº∫Âà∂ÈáçÊñ∞ÂàÜÊûêÂêØÂä®Â§±Ë¥•:', error)
        message.error('Âº∫Âà∂ÈáçÊñ∞ÂàÜÊûêÂêØÂä®Â§±Ë¥•Ôºö' + (error.response?.data?.detail || error.message))
      } finally {
        reanalyzing.value = false
      }
    }

    return {
      loading,
      analyzing,
      reanalyzing,
      feedbackList,
      selectedFeedback,
      showCreateDialog,
      showViewDialog,
      editingFeedback,
      feedbackFormRef,
      searchQuery,
      statusFilter,
      categoryFilter,
      pagination,
      feedbackForm,
      feedbackColumns,
      rowSelection,
      getStatusColor,
      getStatusLabel,
      getCategoryColor,
      getCategoryLabel,
      getPriorityColor,
      getPriorityText,
      getFeedbackTitle,
      getFeedbackContent,
      needsExpansion,
      toggleExpansion,
      getSourceIcon,
      getSourceLabel,
      getSourceColor,
      getStatusClass,
      getRelativeTime,
      formatAbsoluteTime,
      getSentimentColor,
      getSentimentLabel,
      getAnalysisMethodColor,
      getAnalysisMethodLabel,
      getModelLabel,
      loadFeedback,
      handleSearch,
      resetFilters,
      handleTableChange,
      viewFeedback,
      editFeedback,
      analyzeFeedback,
      deleteFeedback,
      saveFeedback,
      cancelEdit,
      resetForm,
      triggerAnalyzeAll,
      triggerForceReanalyze,
    }
  },
}
</script>

